'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { useSession } from 'next-auth/react';

export default function ListingManagement() {
  const [listings, setListings] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const router = useRouter();
  const { data: session } = useSession();

  useEffect(() => {
    fetchListings();
  }, []);

  const fetchListings = async () => {
    try {
      const response = await fetch('/api/properties/my-listings', {
        credentials: 'include'
      });
      
      if (!response.ok) {
        throw new Error(await response.text());
      }

      const data = await response.json();
      setListings(data.properties || []);
      setError(null);
    } catch (error) {
      console.error('Failed to fetch listings:', error);
      setError('Failed to load listings. Please try again later.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleDelete = async (listingId) => {
    if (!confirm('Are you sure you want to delete this listing?')) return;

    try {
      const response = await fetch(`/api/listings/${listingId}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        if (response.status === 401) {
          router.push('/login');
          return;
        }
        throw new Error('Failed to delete listing');
      }
      
      setListings(listings.filter(listing => listing._id !== listingId));
    } catch (error) {
      console.error('Failed to delete listing:', error);
      alert('Failed to delete listing. Please try again.');
    }
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center py-8">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center py-8 text-red-500">
        {error}
      </div>
    );
  }

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-semibold">My Listings</h2>
        <Link 
          href="/listings/create" 
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition-colors"
        >
          Add New Listing
        </Link>
      </div>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {listings.map((listing) => (
          <div key={listing._id} className="border rounded-lg p-4 shadow hover:shadow-md transition-shadow">
            <div className="relative pb-[60%] mb-4">
              <img 
                src={listing.images[0] || '/placeholder.jpg'} 
                alt={listing.title}
                className="absolute inset-0 w-full h-full object-cover rounded"
              />
            </div>
            <h3 className="font-semibold text-lg mb-2">{listing.title}</h3>
            <p className="text-gray-600 mb-2">${listing.price.toLocaleString()}</p>
            <div className="flex space-x-2">
              <Link 
                href={`/listings/edit/${listing._id}`}
                className="flex-1 text-center bg-gray-100 text-gray-700 px-3 py-1.5 rounded hover:bg-gray-200 transition-colors"
              >
                Edit
              </Link>
              <button
                onClick={() => handleDelete(listing._id)}
                className="flex-1 bg-red-100 text-red-700 px-3 py-1.5 rounded hover:bg-red-200 transition-colors"
              >
                Delete
              </button>
            </div>
          </div>
        ))}
      </div>

      {listings.length === 0 && (
        <div className="text-center py-12 bg-gray-50 rounded-lg">
          <p className="text-gray-500 mb-4">No listings found.</p>
          <Link 
            href="/listings/create"
            className="text-blue-500 hover:text-blue-600 transition-colors"
          >
            Create your first listing â†’
          </Link>
        </div>
      )}
    </div>
  );
}
