'use client';

import { useState, useEffect } from 'react';

export default function EnquiryManagement() {
  const [enquiries, setEnquiries] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    fetchEnquiries();
  }, []);

  const fetchEnquiries = async () => {
    try {
      const response = await fetch('/api/enquiries/my-enquiries');
      if (response.ok) {
        const data = await response.json();
        setEnquiries(data);
      }
    } catch (error) {
      console.error('Failed to fetch enquiries:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleStatusUpdate = async (enquiryId, newStatus) => {
    try {
      const response = await fetch(`/api/enquiries/${enquiryId}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ status: newStatus }),
      });

      if (response.ok) {
        setEnquiries(enquiries.map(enquiry => 
          enquiry._id === enquiryId 
            ? { ...enquiry, status: newStatus }
            : enquiry
        ));
      }
    } catch (error) {
      console.error('Failed to update enquiry status:', error);
    }
  };

  if (isLoading) {
    return <div>Loading enquiries...</div>;
  }

  return (
    <div>
      <h2 className="text-xl font-semibold mb-6">Enquiries</h2>

      <div className="space-y-4">
        {enquiries.map((enquiry) => (
          <div key={enquiry._id} className="border rounded-lg p-4 shadow">
            <div className="flex justify-between items-start mb-4">
              <div>
                <h3 className="font-semibold">
                  {enquiry.listing?.title || 'Listing Unavailable'}
                </h3>
                <p className="text-gray-600">From: {enquiry.name}</p>
                <p className="text-gray-600">Email: {enquiry.email}</p>
                <p className="text-gray-600">Phone: {enquiry.phone}</p>
              </div>
              <select
                value={enquiry.status}
                onChange={(e) => handleStatusUpdate(enquiry._id, e.target.value)}
                className="border rounded p-1"
              >
                <option value="pending">Pending</option>
                <option value="contacted">Contacted</option>
                <option value="resolved">Resolved</option>
                <option value="archived">Archived</option>
              </select>
            </div>
            
            <div className="bg-gray-50 p-3 rounded">
              <p className="text-gray-700">{enquiry.message}</p>
            </div>
            
            <div className="mt-2 text-sm text-gray-500">
              Received: {new Date(enquiry.createdAt).toLocaleDateString()}
            </div>
          </div>
        ))}

        {enquiries.length === 0 && (
          <div className="text-center py-8 text-gray-500">
            No enquiries received yet.
          </div>
        )}
      </div>
    </div>
  );
}
